import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Parser from "html-react-parser"
import { getShareButton } from "../components/Careers/PositionCard/positionCard"

class Job extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        firstName : "",
        lastName: "",
        email: "",
        phone: "",
        loaded: "",
        experience: "",
        resume: "",
        location: "",
        githubLink: "",
        sourceFrom: "",
        fileLabel: "Please upload your resume",
        loading: false,
        error: false,
        errorMessage: ''
    }    

    this.handleChange = this.handleChange.bind(this);
    this.onFileChange = this.onFileChange.bind(this);
    
}

handleChange(evt) {
    // var validated = false;
    this.setState({
        [evt.target.name]: evt.target.value
    });

    // if (evt.target.checkValidity() === false) {
    //     evt.target.classList.add('error-invalid');
    //     evt.target.nextElementSibling.classList.add('d-block');
    //     validated = false;
    // } else {
    //     evt.target.classList.remove('error-invalid');
    //     evt.target.nextElementSibling.classList.remove('d-block');
    //     validated = true;
    // }
    
}

handleSubmit = e => {
  e.preventDefault();

  e.preventDefault();

        var validated = false;
        var forms = document.getElementsByClassName('needs-validation');
        Array.prototype.filter.call(forms, function (form) {
            if (form.checkValidity() === false) {
                e.preventDefault();
                e.stopPropagation();

                form.classList.add('was-validated');
                validated = false;
            } else {
                form.classList.add('was-validated');
                validated = true;
            }

        });
        if(validated) {
            this.setState({ loading: true });

            var payload = {
                "fields": [
                  { "key" : "candidate_first_name", "value" : this.state.firstName},
                  { "key" : "candidate_last_name", "value" : this.state.lastName},
                  { "key" : "candidate_email", "value" : this.state.email},
                  { "key" : "candidate_phone", "value" : this.state.phone},
                  { "key" : "how_did_you_hear_about_us", "value" : this.state.sourceFrom},
                  { "key" : "preferred_location", "value" : this.state.location},
                  { "key" : "github_profile", "value" : this.state.githubLink},
                  { "key" : "expected_salary", "value" : this.state.experience},
                  { "key" : "resume", "value": {
                    "encoded_data" :  this.state.resume.split(",").slice(-1)[0],
                    "file_name" : this.state.fileLabel
                    }
                  }
                ],
                "source" : this.props.location.state.referer
            };
            console.log('Payload', payload);
        }
}

onFileChange(e, file) {
  var fileData = file || e.target.files[0],
  reader = new FileReader();
  
  this.setState({ loaded: false });
  this.setState({ fileLabel: e.target.files[0].name });
  
  reader.onload = (e) => {
      this.setState({ 
          resume: reader.result, 
          loaded: true,
      }); 
  }
  
  reader.readAsDataURL(fileData);
}
  render() {
    const {
      description,
      lists,
      categories,
      text,
      descriptionPlain,
      id,
    } = this.props.data.jobsJson

    return (
      <Layout location={this.props.location}>
        <SEO title={text} description={descriptionPlain} />
        <div className="container first-section">
          <div className="d-flex flex-row flex-wrap pb-5 mt-5">
            <div
              id={id}
              name={id}
              className="col-md-12  mt-5  col-12 col-md-6 mt-4 mb-0 px-2"
            >
              <div className=" scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight border-success pt-3">
                <h6 className=" px-4 roboto-regular font-sm text-dark ">
                  {text}
                </h6>
                <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                  <span className="pl-4  text-green col-">
                    {categories.location}
                  </span>
                  <span className="pl-4  text-black col-">
                    {categories.team}
                  </span>
                </div>
                <div>
                  <div className="pl-4 pt-3">
                    <div className="row justify-content-between align-items-center pl-2">
                      <a
                        rel="noopener noreferrer"
                        href="#apply-form"
                        className="apply btn btn-success px-5 apply  my-2"
                      >
                        APPLY NOW
                      </a>
                      <div className="d-none d-md-block">
                        {getShareButton(this.props.data.jobsJson)}
                      </div>
                    </div>
                    <div className="py-5 my-5 py-md-2 my-md-0">
                      <p className="  roboto-bold text-black font-sm">
                        Overview
                      </p>
                      <div className=" description-font text-dark col-12 pl-1">
                        {Parser(description)}
                      </div>
                      <div className="">
                        {lists.map((list, i) => {
                          return (
                            <React.Fragment>
                              <p className="pt-3 roboto-bold text-black font-sm">
                                {list.text}
                              </p>
                              <ul className="pl-3 mb-3">
                                {Parser(list.content)}
                              </ul>
                            </React.Fragment>
                          )
                        })}
                      </div>
                    </div>
                    <div className="d-block text-center"></div>
                  </div>

                  <div className="container" id="apply-form">
                    <h4 className="text-center text-green font-weight-bold text-uppercase">
                      Apply
                    </h4>
                    <form className="needs-validation" noValidate method="POST" onSubmit = {
                    this.handleSubmit
                }>
                      <div className="d-flex flex-row flex-wrap justify-content-around">
                        <div className="col-md-5 col-12">
                          <input
                            type="text"
                            className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                            id="full-name"
                            placeholder="Full name *"
                            required
                            onChange={this.handleChange}
                            name="full-name"
                          />
                          <div className="invalid-feedback">* Required</div>
                        </div>
                        <div className="col-md-5 col-12">
                          <input
                            type="email"
                            className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                            id="email"
                            placeholder="Email *"
                            required
                            onChange={this.handleChange}
                            name="email"
                          />
                          <div className="invalid-feedback">Invalid Email.</div>
                        </div>
                      </div>
                      <div className="d-flex flex-row flex-wrap justify-content-around mt-md-5 mt-sm-0">
                        <div className="col-md-5 col-12">
                          <input
                            type="tel"
                            className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                            id="phone"
                            placeholder="Phone *"
                            pattern="[0-9+?]*"
                            onChange={this.handleChange}
                            name="phone"
                            required
                          />
                          <div className="invalid-feedback">Invalid phone.</div>
                        </div>

                        <div className="col-md-5 col-12">
                          <input
                            type="text"
                            className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                            id="current-company"
                            placeholder="Current Company *"
                            onChange={this.handleChange}
                            name="current-company"
                            required
                          />
                        </div>
                      </div>
                      <div
                        className="d-flex flex-row flex-wrap justify-content-center py-5"
                        id="drop_zone"
                      >
                        <div className="col-11 drag-and-drop border border-success mx-3">
                          <p className="text-muted text-center py-2">
                            {" "}
                            Resume/CV *
                            <br />{" "}
                            <small>
                              Allowed formats are (doc, txt, html, htm, rtf,
                              docx, odt, pdf)
                            </small>
                            <p className="mb-0">{this.state.fileLabel}</p>
                          </p>
                          <div className="pb-1 text-center">
                            <p id="dragFileName"></p>
                            <input
                              required
                              className="d-none form-control-file"
                              type="file"
                              name="resume"
                              id="resume"
                              onChange={this.onFileChange}
                            />
                            <label
                              className="btn btn-success mt-1 px-5 text-white font-weight-bold "
                              htmlFor="resume"
                            >
                              Browse
                            </label>

                            <div className="invalid-feedback">*Required</div>
                          </div>
                        </div>
                      </div>

                      {/* Links */}
                      <div className="d-flex flex-row flex-wrap justify-content-around">
                        <div className="col-md-10 px-0">
                          <div className="row">
                            <div className="col-12 mb-md-3">
                              <h4 className="text-center text-green text-uppercase">Links</h4>
                            </div>
                            <div className="col-4 pl-0">
                              <input
                                type="text"
                                className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                id="linkedin-url"
                                placeholder="LinkedIn URL"
                                onChange={this.handleChange}
                                name="linkedin-url"
                                required
                              />
                              <div className="invalid-feedback">
                                Invalid phone.
                              </div>
                            </div>

                            <div className="col-4">
                              <input
                                type="text"
                                className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                id="github-url"
                                placeholder="GitHub URL"
                                onChange={this.handleChange}
                                name="github-url"
                                required
                              />
                            </div>

                            <div className="col-4 pr-0">
                              <input
                                type="text"
                                className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                id="portfolio-url"
                                placeholder="Portfolio URL"
                                onChange={this.handleChange}
                                name="portfolio-url"
                                required
                              />
                            </div>
                          </div>
                        </div>
                      </div>

                      {/* End Links */}

                      {/* Additional Information */}
                      <div className="d-flex flex-row flex-wrap justify-content-center my-md-5 mt-sm-0">
                        <div className="col-12 mb-3 px-0">
                          <h4 className="text-center text-green">
                            ADDITIONAL INFO
                          </h4>
                        </div>
                        <div className="col-md-10 px-0">
                          <div className="row mt-md-3">
                            <div className="col-md-6 pl-0">
                              <select
                                className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                id="preferred-location"
                                required
                                onChange={this.handleChange}
                                name="preferred-location"
                              >
                                <option value="">Select Work Authorisation *</option>

                                <option value="I am authorised to work for any employer in the
                                  country in which this position is based">
                                  I am authorised to work for any employer in the
                                  country in which this position is based
                                </option>
                                <option value="I require /will require Gojek's sponsorship to get
                                  work work authorisation in the country in which
                                  this position is based (e.g. H-1B, EP, etc.)">
                                  I require /will require Gojek's sponsorship to get
                                  work work authorisation in the country in which
                                  this position is based (e.g. H-1B, EP, etc.)
                                </option>
                                <option value="My status to work in the country in which this
                                  position is unknown">
                                  My status to work in the country in which this
                                  position is unknown
                                </option>
                              </select>
                              <div className="invalid-feedback">Required</div>
                            </div>
                            <div className="col-md-6 pr-0">
                              <select
                                className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                id="preferred-location"
                                required
                                onChange={this.handleChange}
                                name="location"
                              >
                                <option value="">You heard about us on *</option>

                                <option value="gojek.io">gojek.io</option>
                                <option value="Gojek Employee">
                                  Gojek Employee
                                </option>
                                <option value="Advertisements">
                                  Advertisements
                                </option>
                                <option value="Blog">Blog</option>
                                <option value="Conference &amp; Meetups">
                                  Conference &amp; Meetups
                                </option>
                                <option value="Social Media">Social Media</option>
                                <option value="SuperApp Ads">SuperApp Ads</option>
                                <option value="LinkedIn">LinkedIn</option>
                                <option value="Glassdoor">Glassdoor</option>
                                <option value="Job Board">Job Board</option>
                                <option value="Campus Events">Campus Events</option>
                                <option value="Word Of Mouth">Word Of Mouth</option>
                                <option value="Others">Others</option>
                              </select>
                              <div className="invalid-feedback">Required</div>
                            </div>

                            </div>
                            <div className="row mt-md-4">
                              <div className="col-md-6 pl-0">
                                <input
                                  type="text"
                                  className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                  id="referrel"
                                  placeholder="Referral Email"
                                  pattern="[0-9+?]*"
                                  onChange={this.handleChange}
                                  name="referrel"
                                  required
                                />
                                <small className="text-green">If its a referral from a Gojek Employee, please state their name or official email ID of the Employee</small>
                              </div>
                            </div>

                            {/* Textarea 1 */}
                            <div className="row mt-md-4">
                              <div className="col-md-6 pl-0">
                                <textarea
                                  className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                  id="about-you"
                                  placeholder="Tell us what makes you a good fit for this role *"
                                  onChange={this.handleChange}
                                  name="about-you"
                                  required
                                ></textarea>
                                <small className="text-green">Say something that will catch our eye!</small>
                              </div>
                              <div className="col-md-6 pr-0">
                                <textarea
                                  className="form-control border-top-0 border-left-0 border-right-0  rounded-0"
                                  id="cover-letter"
                                  placeholder="Additional information"
                                  onChange={this.handleChange}
                                  name="cover-letter"
                                  required
                                ></textarea>
                                <small className="text-green">Add a cover letter or anything else you want to share.</small>
                              </div>
                          </div>
                        </div>
                      </div>
                      {/* End Other info */}
                      <div className="text-center">
                        <button type="submit" className="apply btn btn-success px-5 apply mb-3">Submit </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}

export default Job
export const pageQuery = graphql`
  query PostPage($id: String!) {
    # Select the post which equals this id.
    jobsJson(id: { eq: $id }) {
      id
      text
      hostedUrl
      descriptionPlain
      description
      createdAt
      applyUrl
      additionalPlain
      additional
      lists {
        content
        text
      }
      categories {
        commitment
        department
        location
        team
      }
    }
  }
`
